<template>
  <div>字典管理

    <p>{{message}}</p>
    <button @click="turn()"> 翻转</button>
  </div>
</template>

<script>
export default {
    name:"DictManage",
    data(){
      return {
        message:"ab ab!"
      }
    },
    methods:{
         turn(){
        this.message=this.message.split("").reverse().join("")
      }
    }
}
</script>

<style scoped>

div{
  font-size: 80px;
}
</style>